<template>
	<div class="conten">
		<el-form ref="form" :model="form" label-width="80px">
		  <el-form-item label="名称">
		    <el-input v-model="form.name"></el-input>
		  </el-form-item>
		  
		  <el-form-item label="价格">
		    <el-input v-model="form.price" placeholder="价格"></el-input>
			<el-input v-model="form.discountPrice" placeholder="折扣价"></el-input>
		  </el-form-item>
		  
		  <el-form-item label="类别">
		    <el-radio-group v-model="form.categoryId">
				<!-- <el-radio v-for="item in form.categoryIS" :label='item.id'></el-radio> -->
		      <el-radio :label="''+item.id" :key="item.id" v-for="item in form.categoryIds">{{item.name}}</el-radio>	    </el-radio-group>
		  </el-form-item>
		  <el-form-item label="备注">
		    <el-input type="textarea" v-model="form.remark"></el-input>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary" @click="onSubmit">立即创建</el-button>
		    <el-button>取消</el-button>
		  </el-form-item>
		</el-form>
	</div>
</template>

<script>
	import {getAllCategory,saveFood} from '../../api/index.js'
	import food from '../../entity/food.js'
	import {dateUtil} from '../../util/dateUtil.js'
export default {
  name: 'HelloWorld',
  data(){
	  return{
		  form: {
		            name: '',
		            categoryId: 0,
					price:0,
					discountPrice:0,
					categoryIds:[],
		            categoryId: 0,
		            remark: ''
		          }
	  }
  },
  methods:{
	  onSubmit(){
		food.createTime= dateUtil()
		food.updateTime = food.createTime
		food.name = this.form.name
		console.log(this.form.categoryId)
		console.log(this.form.categoryId)
		food.categoryId = this.form.categoryId
		food.price = this.form.price
		food.discountPrice = this.form.discountPrice
		food.remark = this.form.remark
		saveFood(food).then( r=>{
			console.log(JSON.stringify(r))
		}) 
	  },
	  
  },
  created:function() {
  	//请求类别数据
	getAllCategory({}).then(r =>{
		console.log(JSON.stringify(r))
		this.form.categoryIds = r.data;
	})
  },
  props: {
    msg: String
  },
}
</script>

<style scss scoped>
	.conten{
		width: 100%;
		height: 100%;
	}
</style>
