<template>
	<!-- <div class="top">
		<div class="left">
			<router-link :to="{name:'FoodAdd'}">添加</router-link>
			<br />
			<br>
			<router-link :to="{name:'FoodChange'}">修改</router-link>
		</div>
		<router-view class="content"></router-view>
	</div> -->
	
	<div>
		<el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
			<el-menu-item :index="(index+'')" v-for="(item,index) in this.categorys" :key="index">
				<span @click="toPath(item)">{{item.name}}</span>
			</el-menu-item>
		</el-menu>

		<el-table :data="foodTableData" style="width: 98%">
			<el-table-column label="菜名" width="180">
			  <template slot-scope="scope">
				  <el-tag>{{scope.row.name}}</el-tag>
			  </template>
			</el-table-column>
			<el-table-column label="状态">
				<template slot-scope="scope">
					<el-tag size="medium" v-if="scope.row.status==1">正常</el-tag>
					<el-tag size="medium" v-if="scope.row.status==2">售完</el-tag>
					<el-tag size="medium" v-if="scope.row.status==3">禁用</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="价格">
				<template slot-scope="scope">
					<el-tag>{{scope.row.price}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="折扣价">
				<template slot-scope="scope">
					<el-tag>{{scope.row.discountPrice}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="备注">
				<template slot-scope="scope">
					<el-tag>{{scope.row.remark}}</el-tag>
				</template>
			</el-table-column>
			<el-table-column label="操作">
			  <template slot-scope="scope">
				<el-button
					style="margin-top: 4px;margin-left: 10px;"
					size="mini" 
					@click="foodHand(scope.$index, scope.row,1)">启用</el-button>
				<el-button
					style="margin-top: 4px;"
					size="mini"
					@click="foodHand(scope.$index, scope.row,2)">售完</el-button>
				<el-button
					style="margin-top: 4px;"
					size="mini"
					@click="foodHand(scope.$index, scope.row,3)">禁用</el-button>
				<el-button
					style="margin-top: 4px;"
					size="mini"
					type="danger"
					@click="foodDel(scope.$index, scope.row)">删除</el-button>
			  </template>
			</el-table-column>
			<el-table-column label="创建人">
				<template slot-scope="scope">
					<el-tag>{{scope.row.createBy}}</el-tag>
				</template>
			</el-table-column>
		  </el-table>
		<el-button  @click="adddialog()">添加</el-button>
	  
		<el-dialog
			title="提示"
			:visible.sync="dialogVisible"
			width="60%">
			<el-form  :model="addFoodForm" label-width="80px" :rules="addFoodrules" ref="ruleForm">
			  <el-form-item label="名称" prop="name">
				<el-input v-model="addFoodForm.name"></el-input>
			  </el-form-item>
			  <el-form-item label="价格" prop="price">
				<el-input v-model="addFoodForm.price" placeholder="价格"></el-input>
			  </el-form-item>
			  <el-form-item label="折扣价" prop="discountPrice">
				  <el-input v-model="addFoodForm.discountPrice" placeholder="折扣价"></el-input>
			  </el-form-item>
			  <el-form-item label="类型">
				  <el-tag>{{addFoodForm.categoryName}}</el-tag>
			  </el-form-item>
			  <el-form-item label="备注">
				<el-input type="textarea" v-model="addFoodForm.remark"></el-input>
			  </el-form-item>
			  <el-form-item>
				<el-button type="primary" @click="onSubmit">立即创建</el-button>
				<el-button type="primary" @click="cancel">取消</el-button>
			  </el-form-item>
			</el-form>
		</el-dialog>
	</div>
	
</template>

<script>
	import {getAllCategory
		,saveFood
		,listFood
		,updateFoodStatusOver
		,delFood} from '../../api/index.js'
	import food from '../../entity/food.js'
	import {dateUtil} from '../../util/dateUtil.js'
	
	export default{
		data(){
			return{
				activeIndex:'',
				dialogVisible:false,
				addCategoryName:'',
				addFoodForm:{
					name: '',
					categoryId: 0,
					price:0,
					discountPrice:0,
					categoryName:"",
					remark: ''
				},
				addFoodrules: {
					name: [
						{ required: true, message: '请输入活动名称', trigger: 'blur' },
						{ min: 2, max: 10, message: '长度在 2到 10 个字符', trigger: 'blur' }
					],
					price: [
						{ required: true, message: '请输入价格', trigger: 'change' }
					],
					discountPrice: [
						{ required: true, message: '请输入折扣价格', trigger: 'change' }
					]
				},
				foodTableData:[
					{"id":7,
					"categoryId":5,
					"type":1,
					"name":"98",
					"sort":0,
					"price":8,
					"discountPrice":8,
					"sales":0,
					"remark":"",
					"status":1,
					"createTime":"2019-10-22 10:49:57",
					"updateTime":"2019-10-22 10:49:57",
					"createBy":"1"},
				],
				category:{},
				categorys:[]
			}
		},
		methods:{
			adddialog(){
				this.dialogVisible = true;
				this.addFoodForm.categoryName = this.category.name;
				this.addFoodForm.categoryId = this.category.id
			},
			onSubmit(){
					food.createTime= dateUtil()
					food.updateTime = food.createTime
					food.name = this.addFoodForm.name
					food.categoryId = this.addFoodForm.categoryId
					food.price = this.addFoodForm.price
					food.discountPrice = this.addFoodForm.discountPrice
					food.remark = this.addFoodForm.remark
					saveFood(food).then( r=>{
						console.log(JSON.stringify(r))
						this.dialogVisible = false;
						this.toPath(this.category)
					}) 
			},
			cancel(){
				this.dialogVisible = false;
			},
			toPath(item){
				this.category = item;
				// console.log(JSON.stringify(item))
				listFood({categoryId:item.id}).then(r =>{
					console.log(JSON.stringify(r.data))
					this.foodTableData = r.data
				})
			},
			handleSelect(key, keyPath){
				// console.log(key,"，，，"+keyPath)
			},
			foodHand(index, row,type){
				console.log(index, row,type);
				updateFoodStatusOver({foodId:row.id,state:type}).then(r =>{
					console.log(JSON.stringify(r.data));
					this.toPath(this.category)
				})
			},
			foodDel(index, row){
				console.log(index,row);
				delFood({foodId:row.id}).then(r=>{
					console.log(JSON.stringify(r.data));
					this.toPath(this.category)
				})
			},
			handleDate(date){
				return date.substring(0,10);
			},
		},
		created:function(){
			//请求类别数据
			if(sessionStorage.categorys==null)
			{
				getAllCategory({}).then(r =>{
					console.log(JSON.stringify(r))
					console.log("状态吗："+r.data.code)
					if(r.data.code ==201){
						this.$router.push('/');
					}
					this.categorys = r.data;
					this.activeIndex = '0'
					this.toPath(this.categorys[0]);
				})
			}
		}
	}
	
</script>

<style scss scoped>
	.top{
		display: flex;
	}
	.left{
		height: 100%;
		width: 20%;
	}
	.content{
		height: 100%;
		width: 80%;
	}
	
</style>
